import {IpcRequest} from "../../shared/types";
import {blocksDir, getBlockDir} from "../constants";
import {BlockStorageType} from "../../lib/GraphLibrary/types/BlockStorage";

const path = require('path');
const fs = require('fs');

process.on("message", () => {
    process.send(GetBlockLibraryFiles());
    process.exit()
})

export default function GetBlockLibraryFiles(): void {
    fs.readdir(blocksDir, (err: never, files: string[]) => {
        if (err) {
            console.log(err);
            return;
        }

        const blocks: BlockStorageType[] = [];
        files.forEach(async (file) => {
            if (file.split(".").pop() === "json") {
                const pathStr = getBlockDir(file);
                const data = fs.readFileSync(pathStr, {encoding: 'utf8', flag: 'r'});
                const jsonData: BlockStorageType = JSON.parse(data);
                const p = path.join(path.dirname(pathStr), `${jsonData.name}.jpg`);
                if (fs.existsSync(p)) {
                    jsonData.imgFile = p;
                } else {
                    jsonData.imgFile = "https://picsum.photos/75"
                }

                blocks.push(jsonData);
            }
        });
        return blocks
    }).catch((err: any) => {
        process.exit();
    });
}
